
import {
  NgModule,
  Component,
  Directive,
  ElementRef,
  Input,
  AfterViewInit
} from "@angular/core";
import { getAttributes } from "@carbon/icon-helpers";

@Component({
	selector: "ibm-icon-watson-health-window-auto20",
  template: `
    <svg
      ibmIconWatsonHealthWindowAuto20
      [ariaLabel]="ariaLabel"
      [ariaLabelledby]="ariaLabelledby"
      [ariaHidden]="ariaHidden"
      [title]="title"
      [isFocusable]="focusable"
      [attr.class]="innerClass">
    </svg>
  `
})
export class WatsonHealthWindowAuto20 {
  @Input() ariaLabel: string;
  @Input() ariaLabelledby: string;
  @Input() ariaHidden: boolean;
  @Input() title: string;
  @Input() focusable: boolean = false;
  @Input() innerClass: string;
}

@Directive({
  selector: "[ibmIconWatsonHealthWindowAuto20]"
})
export class WatsonHealthWindowAuto20Directive implements AfterViewInit {
  static titleIdCounter = 0;

  @Input() ariaLabel: string;
  @Input() ariaLabelledby: string;
  @Input() ariaHidden: boolean;
  @Input() title: string;
  @Input() isFocusable: boolean = false;

  constructor(protected elementRef: ElementRef) {}

	ngAfterViewInit() {
    const svg = this.elementRef.nativeElement;
    svg.setAttribute("xmlns", "http://www.w3.org/2000/svg");

    const domParser = new DOMParser();
    const rawSVG = `<svg focusable="false" preserveAspectRatio="xMidYMid meet" style="will-change: transform;" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 32 32" aria-hidden="true"><path d="M15 4H17V7H15zM25 15H28V17H25zM15 25H17V28H15zM4 15H7V17H4z"></path><path d="M7.55 7.04H9.55V10.04H7.55z" transform="rotate(-45 8.55 8.548)"></path><path d="M21.96 7.55H24.96V9.55H21.96z" transform="rotate(-45 23.454 8.555)"></path><path d="M22.45 21.95H24.45V24.95H22.45z" transform="rotate(-45 23.452 23.446)"></path><path d="M7.05 22.45H10.05V24.45H7.05z" transform="rotate(-45 8.544 23.451)"></path><path d="M4 30H28V32H4zM4 0H28V2H4zM16 10a6 6 0 1 0 6 6A6 6 0 0 0 16 10zm-4 6a4 4 0 0 1 4-4v8A4 4 0 0 1 12 16z"></path></svg>`;
    const svgElement = domParser.parseFromString(rawSVG, "image/svg+xml").documentElement;

    let node = svgElement.firstChild;
    while (node) {
      // importNode makes a clone of the node
      // this ensures we keep looping over the nodes in the parsed document
      svg.appendChild(svg.ownerDocument.importNode(node, true));
      node = node.nextSibling;
    }

    const attributes = getAttributes({
      width: 20,
      height: 20,
      viewBox: "0 0 32 32",
      title: this.title,
      "aria-label": this.ariaLabel,
      "aria-labelledby": this.ariaLabelledby,
      "aria-hidden": this.ariaHidden,
      focusable: this.isFocusable.toString()
    });

    const attrKeys = Object.keys(attributes);
    for (let i = 0; i < attrKeys.length; i++) {
      const key = attrKeys[i];
      const value = attributes[key];
      if (key === "title") {
        continue;
      }
      if (value) {
        svg.setAttribute(key, value);
      }
    }

    if (attributes.title) {
      const title = document.createElement("title");
      title.textContent = attributes.title;
      WatsonHealthWindowAuto20Directive.titleIdCounter++;
      title.setAttribute("id", `watson-health-window-auto20-${WatsonHealthWindowAuto20Directive.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `watson-health-window-auto20-${WatsonHealthWindowAuto20Directive.titleIdCounter}`);
    }
	}
}

@NgModule({
  declarations: [
    WatsonHealthWindowAuto20,
    WatsonHealthWindowAuto20Directive
  ],
  exports: [
    WatsonHealthWindowAuto20,
    WatsonHealthWindowAuto20Directive
  ]
})
export class WatsonHealthWindowAuto20Module {}
